.pricing {
  box-sizing: border-box;
  padding: 32px 20px 34px;
}

.pricing__heading {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 42px;

  text-align: center;

  @include bold(24px, 1.25em, $black);
}

.pricing__packages {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  list-style: none;
}

.pricing__package {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding: 68px 42px 66px;

  background-color: $white;
  border-radius: 10px;
  box-shadow: 0 20px 50px rgba(47, 55, 237, 0.12);

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &--labeled::before {
    content: "Выгодно";
    position: absolute;
    top: 0;
    right: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 62px;
    height: 62px;
    overflow: hidden;

    text-transform: uppercase;

    background-color: $blue;
    border-radius: 50%;

    @include bold(8.22px, 1.25em, $white);
  }
}

.pricing__name {
  order: 2;
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 40px;

  font-weight: 600;

  @include regular(16px, 1.59em, $blue);
}

.pricing__cost {
  order: 1;
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 14px;

  @include bold(20px, 1.59em, $blue);
}

.pricing__order-button {
  order: 3;
  align-self: stretch;
  box-sizing: border-box;
}
